/*ul*/
.c-NavBar {
  margin-bottom: 0;
  font-size: 0.75rem;
  font-weight: 700;
  white-space: nowrap;
  text-transform: uppercase;
}
.c-NavBar > * {
  --lh: calc(0.75 * var(--line-height) * 1rem);
}
@media screen and (min-width: 75em) {
  .c-NavBar {
    font-size: 1rem;
  }
  .c-NavBar > * {
    --lh: calc(1 * var(--line-height) * 1rem);
  }
}
.c-NavBar a {
  display: block;
  line-height: inherit;
}
.c-NavBar > * {
  transition: background-color 250ms;
}
.c-NavBar > *:hover,
.c-NavBar > *:focus {
  background-color: rgba(255, 255, 255, 0.25);
}
.c-NavBar > * > a {
  padding: 0.75rem;
}
@media screen and (min-width: 75em) {
  .c-NavBar > * > a {
    padding: 1rem;
  }
}
.c-NavBar--main {
  padding-left: 1rem;
  justify-content: flex-start;
}
.c-NavBar--main > :hover,
.c-NavBar--main > :focus,
.c-NavBar--main > .active {
  background-color: var(--color-accent, #714509);
}
.c-NavBar--main .glyphicons {
  height: 0;
}
.c-NavBar--main .glyphicons-chevron-down {
  padding-left: 1ch;
}
.c-NavBar--register {
  justify-content: center;
  background-color: #0c5fa8;
}
.c-NavBar--contrib {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: flex-start;
  text-shadow: 0 0 4px black;
}
@media not all and (min-width: 60em) {
  .c-NavBar--contrib {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
  }
}
.c-NavBar--contrib > :hover,
.c-NavBar--contrib > :focus {
  background-color: transparent;
}
.c-NavBar--sub {
  justify-content: flex-start;
  font-weight: 600;
  text-transform: none;
}
.c-NavBar--mobile {
  font-weight: 600;
  text-transform: none;
}
.c-NavBar--mobile > * {
  padding: 0.75rem;
}
.c-NavBar--mobile > *:hover,
.c-NavBar--mobile > *:focus {
  background-color: transparent;
}
.c-NavBar--mobile > * > a {
  text-transform: uppercase;
}
.c-NavBar--mobile ul {
  padding: 0;
  list-style: none;
}
.c-NavBar--mobile a {
  padding: 0.25em;
}
